/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { Button, Tabs, DatePicker, Form, Table } from 'antd';
import { BasicTable, DataTable } from '@didi/dantd';
// import { BasicTable, DataTable } from 'components';
import moment from 'moment';
import { columns, data as tableData, newsColumns, news2Columns } from './config';
const { TabPane } = Tabs;

function getOnHourUnix(time) {
  if (!time) {
    return '';
  }
  const onHourDate = new Date(time.format('YYYY-MM-DD 00:00:00'));
  return moment(onHourDate)
    .unix()
    .toString();
}

// const listUrl = 'http://127.0.0.1:7003/list';
const listUrl = 'http://10.168.122.79:8080/list';

const TableDemo = props => {
  const [newsDate, setDate] = useState();
  const [newsData, setNews] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, [newsDate]);

  async function fetchData() {
    const date = getOnHourUnix(newsDate);
    let url = new URL(listUrl) as any;
    url.search = new URLSearchParams({
      date,
    }) as any;
    setLoading(true);
    const res = await fetch(url);
    res
      .json()
      .then(res => {
        setNews(res.data);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }

  const onDateChange = newDate => {
    setDate(newDate);
  };

  console.info('table props', props);

  return (
    <div>
      <Tabs defaultActiveKey="1">
        <TabPane tab="普通Table" key="1">
          <Table
            columns={columns}
            dataSource={tableData}
            pagination={{
              size: 'small',
              showSizeChanger: true,
              pageSizeOptions: ['10', '20', '50', '100'],
            }}
          />
        </TabPane>
        <TabPane tab="自定义参数" key="2">
          <BasicTable showSearch={true} columns={columns} dataSource={tableData} />
        </TabPane>
        <TabPane tab="头条新闻·Basic" key="3">
          <BasicTable
            leftHeader={
              <Form.Item
                style={{
                  marginBottom: 0,
                }}
                label="发布日期"
                labelCol={{
                  span: 8,
                }}
                wrapperCol={{
                  span: 16,
                }}
              >
                <DatePicker placeholder="请选择发布日期" onChange={onDateChange} value={newsDate} />
              </Form.Item>
            }
            loading={loading}
            onReload={() => {
              fetchData();
            }}
            tableTitle="头条新闻"
            showSearch={true}
            columns={newsColumns}
            dataSource={newsData}
          />
        </TabPane>
        <TabPane tab="头条新闻·Data" key="4">
          <DataTable
            columns={news2Columns}
            url={listUrl}
            apiCallback={data => {
              // data
              // total
              return data;
            }}
            leftHeader={<Button>一个按钮</Button>}
            pageParams={{
              curPageName: 'cur_page',
              pageSizeName: 'page_size',
              startPage: 1,
            }}
            searchParams={{
              apiName: 'search',
              placeholder: '模糊搜索表格内容(多个关键词请用空格分隔。如：key1 key2)',
            }}
          />
        </TabPane>
      </Tabs>
    </div>
  );
};

export default TableDemo;
